import React from "react";
import PropTypes from "prop-types";

import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";

import "./StateCard.scss";

class StateCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.render = this.render.bind(this);
  }

  render() {
    const { avatar, header, children } = this.props;

    let avatarElement = null;
    if (avatar) {
      avatarElement = (
        <Row className="position-absolute StateCard__avatar--container">
          <Col>
            <img
              src={avatar}
              alt="state card avatar"
              className="StateCard__avatar bg-white rounded rounded-circle border border-dark"
            />
          </Col>
        </Row>
      );
    }

    return (
      <Col className="px-3 pb-3 d-flex flex-column bg-dark text-white fw-bold position-relative StateCard">
        {avatarElement}
        <Row>
          <h1 className="fs-2 fw-bold text-uppercase mt-2">{header}</h1>
        </Row>
        <Row className="fs-4 flex-column justify-content-center StateCard__body my-2">
          {children}
        </Row>
      </Col>
    );
  }
}

StateCard.propTypes = {
  avatar: PropTypes.string,
  children: PropTypes.element,
  header: PropTypes.string,
};

StateCard.defaultProps = {
  avatar: "",
  children: (<p />),
  header: "",
};

export default StateCard;
